
export default {
  setApr(that){ //利率正则
    if(that.value != '' )
    {
      if(that.value.search(/^([1-9]\d{0,1}|0)(?:\.\d{0,2})?$/)==-1)
      {
        that.value = '';
        that.value= that.value2 ? that.value2 : '';
      }
      else
      {
        that.value2 = that.value;
      }
    }
  },
  setMoney(that, lens){ //金额正则
    if(that.value != '' )
    {
      if(that.value.search(/^([1-9]\d*|0)(?:\.\d{0,2})?$/)==-1)
      {
        that.value = '';
        that.value= that.value2 ? that.value2 : '';
      }
      else if(that.value.length > lens){
        that.value = '';
        that.value= that.value2 ? that.value2 : '';
      }
      else
      {
        that.value2 = that.value;
      }

    }
  },

}



